.header {
    background-color: #333;
    color: white;
    padding: 10px 20px;
  }
  
  .header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .header-title {
    font-size: 24px;
    margin: 0;
  }
  
  .header-nav {
    display: flex;
    gap: 20px;
  }
  
  .header-nav a {
    color: white;
    text-decoration: none;
    font-size: 16px;
  }
  
  .header-nav a:hover {
    text-decoration: underline;
  }
  